  font-weight: 500;
  font-style: normal; }

* {
  box-sizing: inherit; }
  *::before {
    box-sizing: inherit; }
  *::after {
    box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Oswald", "Arial", sans-serif; }

.visuallyhidden:not(:focus):not(:active),
input[type="radio"].visuallyhidden,
input[type="checkbox"].visuallyhidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

